import React from 'react'
import {Toast, Button, WingBlank, InputItem, WhiteSpace, List, Picker } from 'antd-mobile';
import axios from 'axios'
import { district } from 'antd-mobile-demo-data';

const Item = List.Item;

class Business extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            loading: false,
            area: '',
            name: '',
            address: '',
            contact: '',
            phone: '',
            email: '',
            baseurl: apiurl,
        }

        this.androidInputBugFix();
    }

    componentWillMount(){
        document.title = '添加商家';
    }

    androidInputBugFix(){
        if (/Android/gi.test(navigator.userAgent)) {
            window.addEventListener('resize', function () {
                if (document.activeElement.tagName == 'INPUT' || document.activeElement.tagName == 'TEXTAREA') {
                    window.setTimeout(function () {
                        document.activeElement.scrollIntoViewIfNeeded();
                    }, 0);
                }
            })
        }
    }

    apply(){
        if(!this.state.name){
            Toast.fail('请输入用户名', 2);
            return false;
        }

        if(!this.state.contact){
            Toast.fail('请输入联系人', 2);
            return false;
        }

        if(!this.state.phone){
            Toast.fail('请输入联系电话', 2);
            return false;
        }

        if(!this.state.email){
            Toast.fail('请输入电子邮箱', 2);
            return false;
        }

        if(!this.state.area){
            Toast.fail('请选择地区', 2);
            return false;
        }

        if(!this.state.address){
            Toast.fail('请输入详细地址', 2);
            return false;
        }

        if(!this.state.loading) {
            Toast.loading('提交中…', 30);
            this.setState({
                loading: true
            });

            axios({
                method: 'post',
                url: this.state.baseurl + '/admins/apply',
                data: {
                    name: this.state.name,
                    contact: this.state.contact,
                    phone: this.state.phone,
                    email: this.state.email,
                    area: JSON.stringify(this.state.area),
                    address: this.state.address
                }
            }).then(function (response) {
                if (response.status == 200 && response.data.code == 0) {
                    this.setState({
                        loading: false,
                        name: '',
                        contact: '',
                        phone: '',
                        email: '',
                        area: '',
                        address: ''
                    });
                    Toast.success('请待管理员审核', 5);
                    setTimeout(()=>{
                        history.back();
                    }, 5000)
                }
                else {
                    this.setState({
                        loading: false
                    });
                    Toast.fail(response.data.message, 2);
                }
            }.bind(this)).catch(function (error) {
                this.setState({
                    loading: false
                });
                Toast.fail('未知错误', 2);
            }.bind(this));
        }
    }

    render() {
        return (
            <div className="d-container">
                <List renderHeader={() => '商家信息'}>
                    <InputItem
                        value={this.state.name}
                        onChange={v => this.setState({ name: v })}
                        placeholder="商家名称"
                    >商家名称</InputItem>
                    <InputItem
                        value={this.state.contact}
                        onChange={v => this.setState({ contact: v })}
                        placeholder="联系人"
                    >联系人</InputItem>
                    <InputItem
                        value={this.state.phone}
                        onChange={v => this.setState({ phone: v })}
                        placeholder="联系电话"
                    >联系电话</InputItem>
                    <InputItem
                        value={this.state.email}
                        onChange={v => this.setState({ email: v })}
                        placeholder="电子邮箱"
                    >电子邮箱</InputItem>
                    <Picker extra="请选择地区"
                            data={district}
                            title="请选择地区"
                            value={this.state.area}
                            onChange={v => this.setState({ area: v })}
                            onOk={v => this.setState({ area: v })}
                    >
                        <List.Item arrow="horizontal">选择地区</List.Item>
                    </Picker>
                    <InputItem
                        value={this.state.address}
                        onChange={v => this.setState({ address: v })}
                        placeholder="详细地址"
                    >详细地址</InputItem>
                </List>
                <WhiteSpace size="xl" />
                <WingBlank size="lg">
                    <Button className="btn" type="primary" disabled={this.state.loading} loading={this.state.loading} onClick={this.apply.bind(this)}>添加</Button>
                </WingBlank>
                <WhiteSpace size="xl" />
            </div>
        );
    }
}

export default Business




