import React from 'react'
import '../assets/css/scan.css';
import {Toast, Button, WingBlank, InputItem, WhiteSpace, List, Picker, Checkbox } from 'antd-mobile';
import axios from 'axios'
import { Link } from 'react-router';

const CheckboxItem = Checkbox.CheckboxItem;
const Item = List.Item;

class Scan extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            loading: false,
            did: '',
            sn: '',
            baseurl: apiurl,
            address: '',
            location: '',
            agent: [],
            pricelist: [],
            ptplOptions: ptpls,
            agentOptions: agents,
            hasData: false,
            latitude: '',
            longitude: ''
        }

        this.init();
    }

    componentDidMount(){
        let temp = [];
        this.state.ptplOptions.map((item, idx)=>{
            temp.push({
                id: item.value,
                checked: true
            });
        });

        this.setState({
            pricelist: temp
        });
    }

    componentWillMount(){
        document.title = '添加设备';
    }

    init(){
        var that = this;
        if(typeof WeixinJSBridge == "undefined"){
            if(document.addEventListener){
                document.addEventListener('WeixinJSBridgeReady', wechat, false);
            }
            else if(document.attachEvent){
                document.attachEvent('WeixinJSBridgeReady', wechat);
                document.attachEvent('onWeixinJSBridgeReady', wechat);
            }
        }
        else{
            wechat();
        }

        function wechat(){
            wx.config({
                debug:false,
                signature: signature,
                appId: appId,
                timestamp: timeStamp,
                nonceStr: nonceStr,
                jsApiList: ['scanQRCode', 'getLocation'],
            });
            wx.ready(function(){
                that.getLocation();
            });

            wx.error(function(res){
                Toast.fail('启动失败', 2);
            });
        }
    }

    scan(){
        var that = this;
        wx.scanQRCode({
            needResult: 1, // 默认为0，扫描结果由微信处理，1则直接返回扫描结果，
            scanType: ["qrCode"], // 可以指定扫二维码还是一维码，默认二者都有
            success: function (res) {
                var result = res.resultStr; // 当needResult 为 1 时，扫码返回的结果
                var index = result.indexOf('?');
                var second_index = index + 1;
                var did_str = result.substring(second_index);
                var did_arr = did_str.split('=');

                that.setState({
                    did: did_arr[1]
                });

                that.getTpl(did_arr[1]);
            }
        });
    }

    getLocation(){
        var that = this;
        Toast.loading('定位中…', 3);
        wx.getLocation({
            type: 'wgs84', // 默认为wgs84的gps坐标，如果要返回直接给openLocation用的火星坐标，可传入'gcj02'
            success: function (res) {
                var latitude = res.latitude; // 纬度，浮点数，范围为90 ~ -90
                var longitude = res.longitude; // 经度，浮点数，范围为180 ~ -180。
                var geocoder = new qq.maps.Geocoder();
                var coord = new qq.maps.LatLng(latitude, longitude);

                that.setState({
                    latitude: latitude,
                    longitude: longitude
                });

                geocoder.getAddress(coord);
                geocoder.setComplete(function(result) {
                    that.setState({
                        address: result.detail.addressComponents.city
                    });
                });

                Toast.hide();
            },
            fail: function(res){
                Toast.hide();
            }
        });
    }

    ptplChange = (e, val) => {
        let temp = [];
        this.state.pricelist.map((item, idx)=>{
            if(item.id == val){
                item.checked = e.target.checked ? true : false;
            }
            temp.push(item);
        });

        this.setState({
            pricelist: temp
        });
    }

    getTpl(did){
        if(!this.state.loading) {
            Toast.loading('加载中…', 30);
            this.setState({
                loading: true
            });

            axios({
                method: 'post',
                url: this.state.baseurl + '/api/devices/tpls',
                data: {
                    did: did
                },
            }).then(function (response) {
                Toast.hide();
                if (response.status == 200 && response.data.code == 0) {
                    let temp = [];
                    response.data.data.ptpls.map((item, idx)=>{
                        temp.push({
                            id: item.value,
                            checked: true
                        });
                    });

                    this.setState({
                        loading: false,
                        hasData: true,
                        sn: response.data.data.sn,
                        ptplOptions: response.data.data.ptpls,
                        pricelist: temp
                    });
                }
                else {
                    Toast.fail('设备不存在', 3);
                    this.setState({
                        loading: false,
                        hasData: false,
                    });
                }
            }.bind(this)).catch(function (error) {
                Toast.hide();
                Toast.fail('获取失败', 2);
                this.setState({
                    loading: false,
                    hasData: false,
                });
            }.bind(this));
        }
    }

    addressChange(value){
        this.setState({
            address: value
        });
    }

    locationChange(value){
        this.setState({
            location: value
        });
    }

    submit(){
        if(!this.state.did){
            Toast.fail('请扫描设备', 2);
            return false;
        }

        if(!this.state.address){
            Toast.fail('请输入摆放位置', 2);
            return false;
        }

        if(!this.state.location){
            Toast.fail('请输入场地名称', 2);
            return false;
        }

        if(this.state.agent.length <= 0){
            Toast.fail('请选择所属商家', 2);
            return false;
        }

        let idlist = [];
        this.state.pricelist.map((item, idx)=>{
            if(item.checked){
                idlist.push(item.id);
            }
        });

        if(idlist.length <= 0){
            Toast.fail('请选择价格规则', 2);
            return false;
        }

        if(!this.state.loading) {
            Toast.loading('提交中…', 30);
            this.setState({
                loading: true
            });

            axios({
                method: 'post',
                url: this.state.baseurl + '/devices/assign',
                data: {
                    did: this.state.did,
                    address: this.state.address,
                    location: this.state.location,
                    pricelist: idlist,
                    uid: this.state.agent[0]
                }
            }).then(function (response) {
                if (response.status == 200 && response.data.code == 0) {
                    this.setState({
                        loading: false,
                        did: '',
                        address: '',
                        location: ''
                    });
                    Toast.success('提交成功', 2);
                }
                else {
                    this.setState({
                        loading: false
                    });
                    Toast.fail(response.data.message, 2);
                }
            }.bind(this)).catch(function (error) {
                this.setState({
                    loading: false
                });
                Toast.fail('未知错误', 2);
            }.bind(this));
        }
    }

    render() {
        let path = '/business';
        return (
            <div className="d-container">
                <List renderHeader={() => '设备信息'}>
                    <div className="scan-container">
                        <InputItem
                            value={this.state.sn}
                            editable={false}
                            placeholder={'请扫描设备二维码'}
                        >设备SN</InputItem>
                        <svg className="icon scan" ariaHidden="true" onClick={this.scan.bind(this)}>
                            <use xlinkHref="#icon-scan"></use>
                        </svg>
                    </div>
                    <div>
                        <InputItem
                            value={this.state.address}
                            onChange={this.addressChange.bind(this)}
                            placeholder="摆放位置"
                        >摆放位置</InputItem>
                        <InputItem
                            value={this.state.location}
                            onChange={this.locationChange.bind(this)}
                            placeholder="场地名称"
                        >场地名称</InputItem>
                    </div>
                    <Item key={98} arrow="horizontal">
                        <Link to={path}>
                            <div className="add-business">添加商家</div>
                        </Link>
                    </Item>
                    <div style={{display: this.state.agentOptions.length > 0 ? 'block' : 'none'}}>
                        <Picker data={this.state.agentOptions} value={this.state.agent} onChange={v => this.setState({ agent: v })} className="forss" cols={1}>
                            <List.Item arrow="horizontal">所属商家</List.Item>
                        </Picker>
                    </div>
                    <Item key={99} arrow="horizontal" extra="名下还未有商家" style={{display: this.state.agentOptions.length > 0 ? 'none' : 'normal'}}>
                        <div className="add-business">所属商家</div>
                    </Item>
                </List>
                <List renderHeader={() => '价格规则'} style={{display: this.state.did && this.state.hasData ? 'block' : 'none'}}>
                    {this.state.ptplOptions.map((item, index) => (
                        <CheckboxItem key={item.value} onChange={() => this.ptplChange(event, item.value)} defaultChecked={true}>
                            {item.label}
                        </CheckboxItem>
                    ))}
                </List>
                <WhiteSpace size="xl" />
                <WingBlank size="lg">
                    <Button className="btn" type="primary" disabled={this.state.loading && !this.state.did && !this.shasData.hasData} loading={this.state.loading} onClick={this.submit.bind(this)}>添加</Button>
                </WingBlank>
                <WhiteSpace size="xl" />
            </div>
        );
    }
}

export default Scan;



